



































































































































import {Component, Prop, Provide, Vue, Watch} from 'vue-property-decorator';
import {FdTranslator} from '@/FdTranslator';
import SearchTable from "@/components/form/components/SearchTable.vue";


declare let $: any;

@Component({
    name: 'Seo',
    filters: {
        _: function (value) {
            return FdTranslator._(value);
        }
    },
    components: {
        'search-table': SearchTable
    }
})

export default class Seo extends Vue {
    @Prop({default: null})
    item: any;

    @Provide()
    show_canonicals: boolean = false;

    @Provide()
    data_canonicals_url: string = '#';

    @Provide()
    metadata: {
        meta_title: string,
        meta_description: string,
        meta_keywords: string,
        meta_search_keywords: string,
        search_tags?: [string],
        meta_robots?: string,
        canonical?: string,
        canonical_id?: number,
    } = {meta_title: '', meta_description: '', meta_keywords: '', meta_search_keywords: ''};

    @Prop({
        default: function () {
            return ' Robots    web-.          , ' +
                '       .' +
                '      content.   :' +
                'NOINDEX    ;' +
                'NOFOLLOW     ,   ;' +
                'INDEX    ;' +
                'FOLLOW     .' +
                'ALL   INDEX, FOLLOW  NONE   NOINDEX, NOFOLLOW   : INDEX, FOLLOW';
        }
    })
    tooltip_robots: string;

    @Prop({
        default: function () {
            return '  Robots  ,     ..  ;  ,      none, nofollow.' +
                ' Google    NOARCHIVE,       google'
        }
    })
    danger_robots: string;

    @Prop({
        default: function () {
            return '   ,     ( )   ' +
                ' .             ,  ' +
                '   100 ,   ,   ,      . ' +
                '      ,     ,      (frameset)      .';
        }
    })
    tooltip_meta: string;

    @Prop({
        default: function () {
            return '     ,  ,  ,    .   ,' +
                '    ,   ,       .     ' +
                '  30 .        .        , ' +
                '   .      ,         ' +
                '        .'
        }
    })
    tooltip_keywords: string;

    @Watch('metadata')
    onChangeMetadata(newValue, oldValue) {

    }

    mounted(): void {
        this.$set(this, 'metadata', this.item.data);
    }

    setCanonical(item): void {
        let me = this;
        me.metadata.canonical = item.link
    }

}
